body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.score {
  padding-top: 10px;
}

.score-button {
  text-align: center;
  margin-right: 5px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.24);
  border-radius: 5px;
}

.points {
  color: white;
  font-size: 65px;
  background: -webkit-linear-gradient(top, #4286f4, #373b44);
  background: linear-gradient(to bottom, #4286f4, #373b44);
}

.points-btn {
  display: block;
  text-align: right;
}

@media only screen and (max-width: 600px) {
    .points-btn {
      text-align: center;
    }
} 

.advantages, .penalties {
  margin-right: 5px;
  padding: 0;
}

.advantages-number {
  font-size: 50px;
  background: -webkit-linear-gradient(top, #ffcc33, #ffb347);
  background: linear-gradient(to bottom, #ffcc33, #ffb347);
}

.penalties-number {
  font-size: 50px;
  color: white;
  background: -webkit-linear-gradient(top, #ed213a, #93291e);
  background: linear-gradient(to bottom, #ed213a, #93291e);
}

.plus-minus-buttons {
  display: block;
  text-align: center;
}

.fighter {
  border-bottom: 5px solid black;
}

.fighter-name {
  font-size: 40px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
}

.fighter-team {
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
}

.timer {
  text-align: center;
  color: #E5D88E;
}

button {
  color: black;
  background-color: gray;
}

.timer-clock {
  font-size: 12vw;
  color: #67E76A;
  font-weight: 600;
}

@media only screen and (max-width: 600px) {
  .timer-clock {
    font-size: 20vw;
  }
} 

.timer-min {
  text-align: right;
  padding-right: 0;
}

.timer-min-btn {
  text-align: center;
}

.timer-sec{
  text-align: left;
  padding-left: 0;
}

.timer-sec-btn {
  text-align: center;
}

.match {
  background: black;
}

@media only screen and (min-width: 600px) {
  .match {
    padding-left: 30px;
  }
} 

.match-title {
  font-size: 40px;
  color: #E5D88E;
  display: block;
  text-align: center;
  font-weight: 600;
  text-transform: uppercase;
}

.match-location-stats {
  background: #162A48;
  height: 100%;
  margin: auto;
}

.match-details{
  margin: auto;
}

.font-white {
  color: white;
}

.font-tan {
  color: #E5D88E;
}

.font-tan, .font-white {
  display: block;
  text-align: center;
  font-size: 20px;
}
require


.controls input, .age-catagory, .male-female, .belt, .weight-catagory {
  margin-left: 5px;
  width: 175px;
}

.controls-gear-icon {
  margin-right: 2px;
  height: 20px;
  display: table-cell;
}


.dropbtn {
  display: table;
  margin-left: 15px;
  text-align: center;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.24);
  border-radius: 5px;
  background: gray;
  padding: 5px;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

.dropbtn span {
  display: table-cell;
  vertical-align: middle;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-title {
  padding: 5px;
  text-align: center;
  color: black;
  text-transform: uppercase;
}

.dropdown-content {
  color: white;
  margin-left: 15px;
  padding: 10px;
  text-align: right;
  position: absolute;
  background: -webkit-linear-gradient(top, #dd1818, #333333);
  background: linear-gradient(to bottom, #dd1818, #333333);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown:hover, .dropdown-content {
  display: block;
}

.repeat-round{
  display: inline;
  margin-left: 30px;
}

.repeat-round-radio-btn {
  width: 46px !important;
  margin-bottom: 10px;
}

.repeat-round-time{
  display: inline;
  margin-left: 12px;
}

.repeat-round-time-field {
  text-align: center;
  width: 50px !important;
}

.controls-done-btn {
  margin: auto;
  display: inherit;
  margin-top: 15px;
}

